import React from 'react';
import Loadable from 'react-loadable';
import { PastePanelProps } from './pastePanel';
import usePreviewCopy from '../../common/hooks/usePreviewCopy';
import { hideCustomPastePanel } from '../actions';

const PastePanel = Loadable({
  loader: () =>
    import(/* webpackChunkName: "clipboard-pastepanel" */ './pastePanel'),
  loading: () => null,
});

export default function LazyPastePanel(props: PastePanelProps) {
  const { controller, config } = props;
  const { _clipboardPanel = '' } = controller.value.data;

  usePreviewCopy(controller);

  React.useEffect(() => {
    if (!config?.pastePanel && _clipboardPanel) {
      controller.run('onAction', hideCustomPastePanel());
    }
  });

  if (!config?.pastePanel || !_clipboardPanel) {
    return null;
  }

  return <PastePanel {...props} />;
}
